@import url('https://fonts.googleapis.com/css2?family=Alegreya&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Alegreya&family=Merriweather:ital,wght@0,300;0,400;0,700;0,900;1,300;1,400;1,700;1,900&family=PT+Serif:ital,wght@0,400;0,700;1,400;1,700&display=swap');

body {
  margin: 0;
  font-family: "Alegreya", serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#note{
  max-width: 90%;
  display: flex;
  text-align: left;
  margin: 1rem;
  padding: 1rem;
  border: 1px solid grey;
  border-radius: 1rem;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
}

h1{
  font-family: "Alegreya", serif;
  font-optical-sizing: auto;
  font-weight: 800;
  font-style: normal;
}


.updateButton{
  padding: 0 0.2rem 0 0.2rem;
  margin-left: 1rem;
  font-size: 0.6rem;
  font-weight:600;
  border-radius: 0.4rem;
  max-height: 1.5rem;
  color: rgb(5, 5, 102);
  border: 1px solid gray;
}